<template>
  <div class="home">
    <div class="page-container">
      <div class="button-container">
        <button
          v-touch:tap="touchCreateWallet"
          class="mid-round-button-blue-filled"
        >
          {{ $t('common.createANewWallet') }}
        </button>
        <button
          v-touch:tap="touchAccessWallet"
          class="mid-round-button-green-filled"
        >
          {{ $t('common.accessMyWallet') }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  computed: {
    ...mapGetters({
      wallet: 'wallet'
    })
  },
  methods: {
    touchCreateWallet() {
      this.$router.push('/create-wallet');
    },
    touchAccessWallet() {
      this.$router.push(
        `${
          this.wallet === null || this.wallet === undefined
            ? '/access-my-wallet'
            : '/interface'
        }`
      );
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'HomeLayout.scss';
</style>
