const AccessWalletLayout = () => import('@/layouts/AccessWalletLayout');
const InterfaceLayout = () => import('@/layouts/InterfaceLayout');
const NotFoundLayout = () => import('@/layouts/NotFoundLayout');
const SendCurrencyContainer = () =>
  import('@/layouts/InterfaceLayout/containers/SendCurrencyContainer');
const EthWalletLayout = () => import('@/layouts/EthWalletLayout');
const MobbyWalletLayout = () => import('@/layouts/MobbyWalletLayout');

const HistoryTxContainer = () => import('@/containers/HistoryTxContainer');
const SelectionWalletContainer = () =>
  import('@/containers/SelectionWalletContainer');

const walletChildrens = [
  {
    path: '',
    component: HistoryTxContainer
  },
  {
    path: 'history-tx',
    component: HistoryTxContainer
  },
  {
    path: 'main',
    component: SelectionWalletContainer
  }
];

const router = [
  {
    path: '/access-my-wallet',
    name: 'AccessWalletLayout',
    component: AccessWalletLayout,
    meta: { requiresAuth: false }
  },
  {
    path: '/wallets/eth',
    name: 'Eth wallet',
    component: EthWalletLayout,
    children: walletChildrens
  },
  {
    path: '/wallets/mobby',
    name: 'Mobby wallet',
    component: MobbyWalletLayout,
    children: walletChildrens
  },
  {
    path: '/interface',
    component: InterfaceLayout,
    children: [
      {
        path: '',
        name: 'Main',
        component: SendCurrencyContainer
      },
      {
        path: 'send-transaction',
        name: 'Send Tx',
        component: SendCurrencyContainer
      }
    ]
  },
  {
    path: '*',
    name: '404',
    component: NotFoundLayout,
    meta: { requiresAuth: false }
  }
];

export default router;
