<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      selectedBtn: -1
    };
  },
  computed: {
    ...mapGetters({
      wallet: 'wallet',
      walletNamespace: 'walletNamespace'
    }),
    disabled() {
      return this.wallet ? false : true;
    }
  },
  watch: {
    $route(to) {
      switch (to.fullPath) {
        case `/wallets/${this.walletNamespace}/main`:
          this.selectedBtn = 0;
          break;
        case `/wallets/${this.walletNamespace}/history-tx`:
          this.selectedBtn = 1;
          break;
        case `/wallets/${this.walletNamespace}`:
          this.selectedBtn = 1;
          break;
        default:
          this.selectedBtn = -1;
          break;
      }
    }
  },
  methods: {
    isSelected(index) {
      return !this.disabled && this.selectedBtn === index;
    },
    clickWallet() {
      this.selectedBtn = 0;
      this.goToRoute('/main');
    },
    clickHistory() {
      this.selectedBtn = 1;
      this.goToRoute('/history-tx');
    },
    clickSend() {
      this.selectedBtn = 2;
      this.goToRoute('/send');
    },
    clickReceive() {
      this.selectedBtn = 3;
      this.goToRoute('/receive');
    },
    goToRoute(path) {
      const currentPath = `/wallets/${this.walletNamespace}`;
      this.$router.push(currentPath + path);
    }
  }
};
</script>

